/**
* USER
*/

.user_content{
      width: 100%;
      height: auto;
      padding: 2rem 1.5rem;
      background: linear-gradient(var(--primary) 16rem, var(--bg-200) 16rem);
      border-radius: 1rem;
      border: solid .1rem var(--border);
}

.user_wrapper{
      width: 100%;
      height: auto;
}

.user_header{
      width: 100%;
      height: auto;
      gap: 1rem;
}

.user_data{
      width: 100%;
}

.user_data__info{
      width: 100%;
      height: 12.2rem;
}

.user_info__title{
      align-items: center;
      height: 2.5rem;
      gap: 1rem;
}

.user_info__title h1{
      color: var(--light);
      font-size: 2.4rem;
}

.user_info__title{
      width: 3.5rem;
      height: 2.5rem;
}

.user_data__info p{
      color: var(--light);
      font-size: 1.4rem;
}

.user_data__nav{
      width: 100%;
      height: 3.2rem;
      align-items: center;
      justify-content: center;
      gap: 1.5rem;
}

.user_data__nav a{
      width: 7rem;
      height: 3.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--bg-200);
      border: solid .2rem var(--primary);
      border-radius: .4rem;
      color: var(--light);
      text-transform: uppercase;
      font-size: 1.2rem;
}

.user_data__details{
      width: 100%;
      height: auto;
      margin-top: 3rem;
}

.user_data__details ul{
      width: 100%;
}

.user_data__details ul li{
      height: 3.2rem;
      align-items: center;
      gap: .5rem;
}

.user_data__details ul li *{
      color: var(--primary);
      font-size: 1.8rem;
}

.user_data__details ul li span{
      color: var(--text-300);
      font-size: 1.6rem;
}



.user_avatar{
      width: 14rem;
      height: 16rem;
      flex-shrink: 0;
      padding: 1rem;
      background: linear-gradient(var(--bg-200) 50%, var(--primary) 50%);
      position: relative;
      transform: translateY(6rem);
      border-radius: .4rem;  
}

.user_avatar:before{
      width: 13rem;
      height: 15rem;
      background:  var(--primary);
      top: .5rem;
      left: .5rem;
      border-radius: .4rem;
}

.user_avatar:after{
      width: 12.6rem;
      height: 14.6rem;
      background: var(--bg-200);
      top: .7rem;
      left: .7rem;
      border-radius: .4rem;
}

.user_avatar__img{
      width: 12rem;
      height: 14rem;
      position: relative;
      z-index: 2;
      align-items: center;
      justify-content: center;
      border-radius: .4rem;
      overflow: hidden;
}

.user_avatar__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_about{
      margin-top: 2rem;
      width: 100%;
      height: auto;
}

.user_about p{
      font-size: 1.6rem;
      line-height: 2.2rem;
      color: var(--text-200);
      white-space: pre-line;
}