/**
* RELATION
*/

.relation_wrapper{
      position: fixed;
      width: 100vw;
      max-width: 35rem;
      height: 100vh;
      background: var(--bg-100);
      padding-top: 6rem;
      top: 0;
      left: 0;
      z-index: 10;
      transform: translateX(-100%);
      transition: .3s ease-in-out;
}

.relation_wrapper.active{
      transform: translateX(0);
}

.relation_header{
      height: 4rem;
      width: 100%;
      padding: 0 2rem;
      align-items: center;
      gap: 1rem;
      position: relative;
      z-index: 5;
}

.btn_relation__close{
      width: 4rem;
      height: 3.2rem;
      align-items: center;
      justify-content: center;
      background: none;
}

.btn_relation__close *{
      font-size: 3rem;
      color: var(--light);
}

.relation_header h2{
      color: var(--light);
      font-size: 2.8rem;
      font-weight: 200;
}

.relation_card{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
}

.relation_banner{
      position: relative;
      width: 100%;
      height: 38rem;
      overflow: hidden;
}

.relation_banner__img{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      position: relative;
      overflow: hidden;
}

.relation_banner__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.relation_banner__img:before,
.relation_banner__img:after{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
}

.relation_banner__img:before{
      background: rgba(255,255,255,.3);
      backdrop-filter: blur(5px);
}

.relation_banner__img:after{
      background: linear-gradient(rgba(0,0,0,.2), var(--bg-100));
}

.relation_banner__info{
      position: absolute;
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      z-index: 2;
      top: 0;
      left: 0;
      padding-top: 11rem;
}

.relation_avatar{
      width: 13.4rem;
      height: 13.4rem;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 0 .5rem rgba(0,0,0,.2);
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
}

.relation_avatar img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.relation_banner__info h3{
      font-size: 2.4rem;
      color: var(--light);
      margin-top: 1rem;
}

.relation_banner__info strong{
      color: var(--primary);
      font-size: 2rem;
}

.relation_navigator{
      width: 100%;
      height: 3.2rem;
      margin-top: 2rem;
      align-items: center;
      justify-content: center;
      gap: 2rem;
}

.relation_navigator a{
      width: 8rem;
      height: 3.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      color: var(--light);
      border: solid .2rem var(--primary);
      border-radius: 999px;
      font-size: 1.2rem;
      transition: .2s ease-in-out;
}

.relation_navigator a:hover{
      background: var(--primary);
}

.relation_about{
      margin-top: 3rem;
      width: 100%;
      height: auto;
      padding: 0 2rem;
}

.relation_about p{
      white-space: pre-line;
      color: var(--text-200);
}