/**
* BANNER
*/

.banner_content{
      width: 100%;
      height: 28rem;
      border-radius: 1rem;
      background: var(--bg-200);
      overflow: hidden;
}

.banner_content__img{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.banner_content__img img{
      min-width: 100%;
      min-height: 100%;
      display: block;
}
