/**
* SOCIAL MENU
*/

.social_menu{
      width: 100%;
      height: auto;
      flex-direction: column;
      align-items: center;
      margin-top: 3rem;
      gap: 1rem;
}

.btn_social__link{
      width: 4rem;
      height: 4rem;
      align-items: center;
      justify-content: center;
      color:  var(--text-100);
      transition: .2s ease-in-out;
}

.btn_social__link:hover{
      color: var(--secundary);
}

.btn_social__link *{
      font-size: 2.4rem;
}