/**
* MODAL
*/

.modal_content{
      width: 100%;
      height: 100vh;
      position: fixed;
      background: rgba(0,0,0,.5);
      top: 0;
      left: 0;
      z-index: 50;
      opacity: 0;
      visibility: hidden;
      align-items: center;
      justify-content: center;
      transition: .3s ease-in-out;
}

.modal_content.active{
      opacity: 1;
      visibility: visible;
}

.modal_inner{
      width: fit-content;
      max-width: 80%;
      height: fit-content;
      max-height: 80vh;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.close_modal{
      position: absolute;
      width: 5rem;
      height: 5rem;
      background: var(--primary);
      bottom: 0;
      right: 0;
      align-items: center;
      justify-content: center;
}

.close_modal *{
      color: var(--light);
      font-size: 2.4rem;
}