/**
* ONLINE FRIEND
*/

.online_friend{
      width: 1.8rem;
      height: 1.8rem;
      background: var(--bg-200);
      border: solid .4rem var(--bg-100);
      position: absolute;
      border-radius: 50%;
      bottom: 0;
      right: 0;
}

.online_friend.green{
      background: var(--green);
}

.online_friend.red{
      background: var(--red);
}