/**
* ONLINE
*/

.online_content{
      height: 2.8rem;
      padding: .7rem;
      background: rgba(0,0,0,.1);
      width: fit-content;
      border-radius: 999px;
      margin-top: 1rem;
      align-items: center;
      gap: .5rem;
}

.online_dot{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}

.online_content span{
      color: var(--light);
      font-size: 1.4rem;
      padding-right: .5rem;
}