/**
* VIDEO
*/

.video_content{
      width: 100%;
      height: auto;
      position: relative;
      margin-top: 3rem;
      margin-bottom: 3rem;
}

.video_content:before{
      width: .1rem;
      height: 3rem;
      background: var(--border);
      top: 0;
      left: 50%;
      transform: translate(-50%, -100%);
}

.video_content__box{
      width: 100%;
      height: 21.8rem;
      background: var(--bg-300);
      border-radius: 1rem;
      overflow: hidden;
}

.video_content__box iframe{
      border: none;
      outline: none;
}

.video_content__title{
      position: relative;
      width: 100%;
      height: auto;
      padding: 1rem;
      margin-top: 2rem;
      border: solid .1rem var(--border);
      border-radius: .6rem;
      align-items: center;
      gap: 1rem;
}

.video_content__title:before{
      width: .1rem;
      height: 2rem;
      background: var(--border);
      top: 0;
      left: 50%;
      transform: translate(-50%, -100%);
}

.video_content__title *{
      color: var(--primary);
}

.video_content__title p{
      color: var(--text-300);
}