/**
* GALLERY
*/

.gallery_content{
      margin-top: 3rem;
      width: 100%;
}

.gallery_title{
      width: 100%;
      height: 5rem;
      position: relative;
      align-items: center;
      justify-content: center;
}

.gallery_title:before,
.gallery_title:after{
      width: calc(50% - 3.5rem);
      height: .1rem;
      background: var(--border);
}

.gallery_title:before{
      left: 0;
}

.gallery_title:after{
      right: 0;
}

.gallery_title__icon{
      width: 5rem;
      height: 5rem;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      border: solid .1rem var(--border);
}

.gallery_title__icon *{
      font-size: 2.8rem;
}

.gallery_wrapper{
      width: 100%;
      height: 34rem;
      align-items: center;
      justify-content: center;
      border-radius: 1rem;
      border: solid .1rem var(--border);
      margin-top: 1.5rem;
      position: relative;
}

.gallery_wrapper:before{
      width: .1rem;
      height: 1.5rem;
      background: var(--border);
      left: 50%;
      top: 0;
      transform: translate(-50%, -100%);
}

.gallery_wrapper__box{
      width: 60rem;
      height: 28rem;
      position: relative;
}

.card_gallery{
      width: 19rem;
      height: 26rem;
      background: var(--bg-100);
      overflow: hidden;
      padding: .5rem;
      transition: .5s ease-in-out;
      position: relative;
      margin-top: 1rem;
}

.card_gallery__img{
      position: relative;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      border-radius: .6rem;
}

.card_gallery__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}


.slick-slide{
      margin: 0rem;
}

button.slick-arrow{
      width: 4rem;
      height: 4rem;
      background: var(--bg-200);
      border-radius: 50%;
      border: solid .1rem var(--border);
      z-index: 2;
      top: 14rem;
}

button.slick-arrow:hover{
      background: var(--bg-200);
}

button.slick-arrow.slick-next{
      right: -3.2rem;
}

button.slick-arrow.slick-prev{
      left: -3.2rem;
}

button.slick-arrow.slick-next:before,
button.slick-arrow.slick-prev:before{
      width: 1.4rem;
      height: 1.4rem;
      color: transparent;
      transform: rotate(45deg);
      top: 1.2rem;
      left: .8rem;
}

button.slick-arrow.slick-next:before{
      border-top: solid .1rem var(--text-100);
      border-right: solid .1rem var(--text-100);
}


button.slick-arrow.slick-prev:before{
      border-bottom: solid .1rem var(--text-100);
      border-left: solid .1rem var(--text-100);
      left: 1.4rem;
}

.card_gallery__inner{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.3);
      opacity: 0;
      transition: .2s ease-in-out;
}

.card_gallery:hover .card_gallery__inner{
      opacity: 1;
}

.card_gallery__inner button{
      width: 4rem;
      height: 4rem;
      position: absolute;
      top: 1.5rem;
      right: 1.5rem;
      border-radius: 50%;
      background: var(--primary);
      align-items: center;
      justify-content: center;
}

.card_gallery__inner button *{
      color: var(--light);
      font-size: 2.4rem;
}