/**
* FRIENDS
*/

.friends_wrapper{
      position: fixed;
      width: 100vw;
      max-width: 35rem;
      height: 100vh;
      top: 0;
      left: 0;
      background: var(--bg-100);
      padding-top: 6rem;
      z-index: 10;
      transform: translateX(-100%);
      transition: .3s ease-in-out;
}

.friends_wrapper.active{
      transform: translateX(0);
}

.friends_header{
      height: 4rem;
      width: 100%;
      align-items: center;
      gap: 1rem;
      padding: 0 2rem;
}

.friends_header h2{
      color: var(--light);
      font-size: 2.8rem;
      font-weight: 200;
}

.btn_friends__close{
      width: 4rem;
      height: 3.2rem;
      background: none;
      align-items: center;
      justify-content: center;
}

.btn_friends__close *{
      color: var(--light);
      font-size: 3rem;
}

.friends_list{
      width: 100%;
      height: auto;
      margin-top: 1rem;
      border-top: solid .1rem var(--border);
}

.friend_row{
      width: 100%;
      height: 8rem;
      background: var(--bg-100);
      border-bottom: solid .1rem var(--border);
      align-items: center;
      padding: 0 2rem;
      gap: 1rem;
}

.friend_row__avatar{
      width: 6rem;
      height: 6rem;
      background: var(--bg-200);
      position: relative;
      border-radius: 50%;
      flex-shrink: 0;
}

.friend_row__avatar_img{
      width: 8rem;
      height: 8rem;
      border-radius: 50%;
      background-color: var(--bg-200);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(.68);
      overflow: hidden;
}

@keyframes move_img{
      100%{
            background-position: right top;
      }
}

.friend_row__data{
      flex: 1;
      align-items: center;
      gap: 1rem;
}

.friend_row__data a{
      width: fit-content;
      height: 2.6rem;
      display: flex;
      align-items: center;
      text-transform: uppercase;
      color: var(--text-300);
      border: solid .1rem var(--border);
      padding: 0 1.5rem;
      border-radius: 999px;
      font-size: 1.2rem;
      font-weight: 500;
      transition: .2s ease-in-out;
}

.friend_row__data a:hover{
      background: var(--primary);
      border: solid .1rem var(--primary);
      color: var(--light);
}

.friend_row__data_info{
      flex: 1;
}

.friend_row__data_info span{
      font-weight: 500;
      font-size: 1.4rem;
}

.friend_row__data_info p{
      color: var(--text-300);
      font-size: 1.4rem;
      margin-top: .25rem;
      font-family: var(--ft-4);
}