/**
* WARNING
*/

.warning_content{
      width: 100%;
      height: auto;
      padding: 2rem;
      border-radius: 1rem;
      border: solid .1rem var(--border);
}

.warning_header{
      height: 4rem;
      align-items: center;
      gap: 1rem;
}

.warning_header *{
      font-size: 2rem;
      color: var(--primary);
}

.warning_header h3{
      font-weight: 400;
      color: var(--text-200);
      font-size: 1.6rem;
}