/**
* MENU 
*/

.menu_content{
      width: 100%;
      position: relative;
      flex-direction: column;
      align-items: center;
}

.menu_content__btns{
      width: 100%;
      flex-direction: column;
      align-items: center;
}

.menu_btn{
      width: 4.6rem;
      height: 4.6rem;
      align-items: center;
      justify-content: center;
      background: none;
      position: relative;
      color: var(--text-100);
      transition: .2s ease-in-out;
}

.menu_btn:hover{
      color: var(--light);
}

.menu_btn *{
      font-size: 2.8rem;
}

.menu_btn span{
      position: absolute;
      width: 2.4rem;
      height: 2.4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--secundary);
      color: var(--light);
      border-radius: 50%;
      font-size: 1rem;
      top: 0;
      right: 0;
      transform: translate(.5rem, .5rem);
}

.menu_content__dots{
      width: .8rem;
      height: .8rem;
      background: none;
      border: solid .2rem var(--border);
      margin-top: 1rem;
      border-radius: 50%;
      position: relative;
}

.menu_content__dots:before,
.menu_content__dots:after{
      width: .8rem;
      height: .8rem;
      background: none;
      border: solid .2rem var(--border);
      border-radius: 50%;
      box-sizing: border-box;
      top: -.2rem;
      left: 0;
}

.menu_content__dots:before{
      transform: translateX(-1.6rem);
}

.menu_content__dots:after{
      transform: translateX(1.2rem);
}